.sidedrawer {
  width: 10%;
  height: 100vh;
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  transform: translateX(-100%);
  transition: all 0.3s ease-out;
  display: inline-block;
  flex-direction: column;
  justify-content: center;
}

.sidedrawer.show {
  transform: translateX(0);
}

.sidedrawer__links {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin-top: 0px;
  padding-left: 0px;
}

.sidedrawer__menu {
  text-align: center;
  user-select: none;
}

.sidedrawer__menu > h2 {
  color: #3ea175;
  font-size: 16px;
  margin-top: 15px;
  margin-bottom: 5px;
  padding: 0 10px;
  font-weight: 700;
}

.sidedrawer__link {
  color: #f3f4f6;
  padding: 10px;
  border-radius: 3px;
  margin-bottom: 5px;
}

.active_menu_link {
  background: rgba(62, 161, 117, 0.3);
  color: #3ea175;
}

.active_menu_link a {
  color: #3ea175 !important;
}

.sidedrawer__link > a {
  text-decoration: none;
  color: #a5aaad;
  font-weight: 700;
}

.sidedrawer__link > i {
  margin-right: 10px;
  font-size: 18px;
}

.sidedrawer_responsive {
  display: inline !important;
  z-index: 9999 !important;
  left: 0 !important;
  position: absolute;
}

.sidedrawer__logoholder {
  margin: 38px auto;
  justify-content: center;
  display: inline-block;
}

.sidedrawer__muckrocklogo {
  width: 60px;
  height: 60px;
  left: 38px;
  top: 38px;
  display: inline-block;

  /* main blue 3E80FF */

  background: #3e80ff;
  border-radius: 10px;
  vertical-align: middle;
  cursor: pointer;
}

.sidedrawer__muckrocklogo h2 {
  height: 100%;
  /* header 2 */
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: inherit;
  text-align: center;
  /* identical to box height */

  /* white fff */

  color: #ffffff;
}

@media only screen and (max-width: 978px) {
  #sidedrawer {
    display: none;
  }

  .sidedrawer__title > i {
    display: inline;
  }
}
